"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { signInWithEmailAndPassword } from "firebase/auth"
import { doc, getDoc } from "firebase/firestore"
import { auth, db, isMockMode } from "@/firebase/firebaseConfig"
import { GlassCard } from "@/components/ui/glass-card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Loader2, Smartphone, ArrowLeft } from "lucide-react"
import { useLanguage } from "@/components/language-provider"

export default function CustomerLogin() {
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState("")
    const router = useRouter()
    const { lang } = useLanguage()

    const handleLogin = async (e: React.FormEvent) => {
        e.preventDefault()
        setLoading(true)
        setError("")

        if (isMockMode) {
            await new Promise(resolve => setTimeout(resolve, 1000))

            if (email === "customer1@example.com" && password === "Customer@123") {
                localStorage.setItem("mock_customer_user", JSON.stringify({
                    email,
                    name: "Sarah Smith",
                    uid: "mock-customer-uid",
                    phone: "+971504444444",
                    address: "Downtown Dubai"
                }))
                router.push("/customer")
            } else if (email === "customer2@example.com" && password === "Customer@123") {
                localStorage.setItem("mock_customer_user", JSON.stringify({
                    email,
                    name: "John Doe",
                    uid: "mock-customer-uid-2",
                    phone: "+971505555555",
                    address: "Business Bay"
                }))
                router.push("/customer")
            } else {
                setError("Invalid credentials. Try: customer1@example.com / Customer@123")
            }
            setLoading(false)
            return
        }

        try {
            const userCredential = await signInWithEmailAndPassword(auth, email, password)

            // Verify user has customer role
            const userDoc = await getDoc(doc(db, "users", userCredential.user.uid))
            if (!userDoc.exists()) {
                setError("Account not found. Please contact support.")
                await auth.signOut()
                setLoading(false)
                return
            }

            if (userDoc.data().role !== "customer") {
                setError("This login is for customers only. Please use the appropriate portal.")
                await auth.signOut()
                setLoading(false)
                return
            }

            router.push("/customer")
        } catch (err: any) {
            console.error("Login error:", err)
            if (err.code === "auth/user-not-found" || err.code === "auth/wrong-password") {
                setError("Invalid email or password")
            } else if (err.code === "auth/too-many-requests") {
                setError("Too many failed attempts. Please try again later.")
            } else {
                setError("Failed to sign in. Please try again.")
            }
        } finally {
            setLoading(false)
        }
    }

    return (
        <div className="min-h-screen flex items-center justify-center p-4">
            <div className="w-full max-w-md">
                <Link
                    href="/"
                    className="inline-flex items-center gap-2 text-white/60 hover:text-white mb-8 transition-colors"
                >
                    <ArrowLeft className="w-4 h-4" />
                    Back to Home
                </Link>

                <GlassCard className="p-8">
                    <div className="text-center mb-8">
                        <div className="w-16 h-16 rounded-2xl bg-gradient-to-br from-cyan-500 to-blue-600 flex items-center justify-center mx-auto mb-4 shadow-lg shadow-cyan-500/25">
                            <Smartphone className="w-8 h-8 text-white" />
                        </div>
                        <h1 className="text-3xl font-bold text-white mb-2">Customer Portal</h1>
                        <p className="text-white/60">Sign in to track your repairs</p>
                    </div>

                    <form onSubmit={handleLogin} className="space-y-6">
                        <div className="space-y-2">
                            <Label htmlFor="email" className="text-white">Email</Label>
                            <Input
                                id="email"
                                type="email"
                                placeholder="your@email.com"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                required
                                className="bg-white/5 border-white/10 text-white placeholder:text-white/30 focus:border-cyan-500/50 focus:ring-cyan-500/20"
                            />
                        </div>

                        <div className="space-y-2">
                            <Label htmlFor="password" className="text-white">Password</Label>
                            <Input
                                id="password"
                                type="password"
                                placeholder="••••••••"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required
                                className="bg-white/5 border-white/10 text-white placeholder:text-white/30 focus:border-cyan-500/50 focus:ring-cyan-500/20"
                            />
                        </div>

                        {error && (
                            <div className="text-red-400 text-sm text-center bg-red-500/10 p-3 rounded-lg border border-red-500/20">
                                {error}
                            </div>
                        )}

                        <Button
                            type="submit"
                            className="w-full bg-gradient-to-r from-cyan-500 to-blue-600 hover:from-cyan-400 hover:to-blue-500 text-white font-semibold h-12"
                            disabled={loading}
                        >
                            {loading ? <Loader2 className="w-4 h-4 animate-spin mr-2" /> : null}
                            Sign In
                        </Button>
                    </form>

                    <div className="mt-8 pt-6 border-t border-white/10">
                        <p className="text-center text-white/60 text-sm">
                            Don't have an account?{" "}
                            <Link href="/book" className="text-cyan-400 hover:text-cyan-300 font-medium">
                                Book a repair
                            </Link>
                        </p>
                    </div>

                    {isMockMode && (
                        <div className="mt-6 p-4 rounded-lg bg-cyan-500/10 border border-cyan-500/20">
                            <p className="text-xs text-cyan-400 font-medium mb-2">Demo Mode - Test Credentials:</p>
                            <p className="text-xs text-white/60">Email: customer1@example.com</p>
                            <p className="text-xs text-white/60">Password: Customer@123</p>
                        </div>
                    )}
                </GlassCard>
            </div>
        </div>
    )
}
